.login-body {
  width: 97vw;
  height: 97vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-container {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 300px;

  & input {
    // padding: 5px;
    padding: 10px;
    margin: 5px 0;
    border: 1px solid gray;
  }

  & button,
  a {
    color: black;
    text-decoration: none;
    text-align: center;
    display: block;
    margin: 0;
    padding: 10px;
    margin: 5px 0;
    background-color: transparent;
    border: 1px solid gray;

    &:hover {
      cursor: pointer;
      background-color: gray;
      color: white;
    }
  }
}

.login-btn {
  display: flex;
  justify-content: space-between;

  &-item {
    flex-basis: 40%;
  }

  &-signup {
    width: 100%;
  }
}